import Swal from "sweetalert2";
import { berhasil } from "./Alert";

export const checkReplikasiValidasi = (lokasi, data) => {
  var searchData = JSON.parse(localStorage.getItem(lokasi)) || [];
  var index = searchData.findIndex(function (item, i) {
    return item.noFaktur === data ? item.noFaktur === data : "TIDAK ADA";
  });
  return index;
};

export const addFaktur = (
  noFaktur,
  tanggal,
  noBon,
  supplier,
  bruto,
  netto,
  bonRp,
  titipRp,
  titipGr,
  keterangan,
  e
) => {
  var tampung = JSON.parse(localStorage.getItem("FakturTerpilih")) || [];
  var hasil = checkReplikasiValidasi("FakturTerpilih", noFaktur);
  if (hasil === -1) {
    var array = {
      tanggal: tanggal,
      noBon: noBon,
      supplier: supplier,
      bruto: bruto,
      netto: netto,
      bonRp: bonRp,
      titipRp: titipRp,
      titipGr: titipGr,
      keterangan: keterangan,
      noFaktur: noFaktur,
      footerDocument: {
        editBy: "Nagatech",
        statusValid: "VALID",
      },
    };
    tampung.push(array);
    localStorage.setItem("FakturTerpilih", JSON.stringify(tampung));
    console.log("BERHASIL TAMBAHKAN : " + noFaktur);
    berhasil("Berhasil Ditambahkan");
  } else {
    Swal.fire({
      position: "top-right",
      icon: "warning",
      text: "Faktur Sudah Terpilih",
      showConfirmButton: false,
      timer: 1500,
    });
  }
};

export const deleteFaktur = (noFaktur) => {
  var tampung = JSON.parse(localStorage.getItem("FakturTerpilih")) || [];
  var hasil = checkReplikasiValidasi("FakturTerpilih", noFaktur);
  tampung.splice(hasil, 1);
  localStorage.setItem("FakturTerpilih", JSON.stringify(tampung));
  berhasil("berhasil dihapus");
  console.log("HASIL TERBARU", JSON.stringify(tampung));
};
